
'use client';

import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
  } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Wrench } from 'lucide-react';
import { useUser } from '@/context/UserContext';
import { useToast } from '@/hooks/use-toast';
import { placeholderTechnicians } from '@/lib/placeholder-data';

export default function LoginPage() {
  const router = useRouter();
  const { user, login, loading } = useUser();
  const { toast } = useToast();
  const [role, setRole] = useState<'Admin' | 'Student'>('Student');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  useEffect(() => {
    if (!loading && user) {
      router.push('/dashboard');
    }
  }, [user, loading, router]);

  const handleLogin = () => {
    if (role === 'Admin') {
        if (email.toLowerCase() === 'criss2l2@gmail.com' && password === 'admin123') {
            login({ email, role });
            router.push('/dashboard');
        } else {
            toast({
                title: 'Invalid Credentials',
                description: 'Please check your email and password.',
                variant: 'destructive',
            });
        }
    } else { // Student Login
        const isTechnician = placeholderTechnicians.some(tech => tech.email.toLowerCase() === email.toLowerCase());
        if (isTechnician) {
            login({ email, role: 'Student' });
            router.push('/dashboard');
        } else {
            toast({
                title: 'Invalid Student/Technician Email',
                description: 'This email is not registered as a technician.',
                variant: 'destructive',
            });
        }
    }
  };
  
  if (loading || (!loading && user)) {
    return (
        <main className="flex min-h-screen flex-col items-center justify-center bg-muted/40 p-4">
            <div>Loading...</div>
        </main>
    );
  }


  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-muted/40 p-4">
      <Card className="w-full max-w-sm">
        <CardHeader className="text-center">
          <div className="mb-4 flex justify-center">
            <div className="rounded-full bg-primary p-3 text-primary-foreground">
              <Wrench className="h-8 w-8" />
            </div>
          </div>
          <CardTitle className="text-2xl">RepairDesk Lite</CardTitle>
          <CardDescription>
            Select a role to sign in to the dashboard.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
            <div className="space-y-2">
                <Label htmlFor="role">Select Role</Label>
                <Select value={role} onValueChange={(value) => {
                    setRole(value as 'Admin' | 'Student');
                    setEmail('');
                    setPassword('');
                }}>
                    <SelectTrigger id="role">
                        <SelectValue placeholder="Select a role" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="Admin">Admin</SelectItem>
                        <SelectItem value="Student">Student</SelectItem>
                    </SelectContent>
                </Select>
            </div>
            
            <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input id="email" type="email" placeholder="user@example.com" value={email} onChange={e => setEmail(e.target.value)} />
            </div>

            <div className="space-y-2">
                <Label htmlFor="password">Password</Label>
                <Input id="password" type="password" value={password} onChange={e => setPassword(e.target.value)} disabled={role === 'Student'} />
            </div>
        

          <Button onClick={handleLogin} className="w-full">
            Sign In
          </Button>
        </CardContent>
      </Card>
    </main>
  );
}
